window.dom = {
    //å¢
    create(string){
        const container = document.createElement('template');
        container.innerHTML = string.trim();
        return container.content.firstChild;
    },
    after(node, node2){   //æ–°å¢å¼Ÿå¼Ÿ
        node.parentNode.insertBefore(node2, node.nextSibling);
    },
    before(node, node2){    //æ–°å¢å“¥å“¥
        node.parentNode.insertBefore(node, node);
    },
    append(parent, node){    //æ–°å¢å„¿å­
        parent.appendChild(node);
    },
    wrap(node,parent){       //æ–°å¢çˆ¶äº²
        dom.before(node, parent)
        dom.append(parent, node)
    },
    //åˆ 
    remove(){  //åˆ é™¤èŠ‚ç‚¹
        node.parentNode.removeChild(node)
        return node
    },
    empty(node){    //åˆ é™¤åä»£
        const array = []
        let x = node.firstChild;
        while(x){
            array.push(dom.remove(node.firstChild))
            x = firstChild
        }
        return array;
    },
    //æ”¹
    attr(node, name, value){     //è¯»å†™å±æ€§
        if(arguments.length === 3){
            node.setAttribute(name, value)
        }else if(arguments === 2){
            return  node.getAttribute(name)
        }
    },
    text(node, string){       //è¯»å†™æ–‡æœ¬å†…å®¹
        if(arguments.length === 3){
            if('innerText' in node){
                node.innerText = string
            }else{
                node.textContent = string
            }
        }else if(arguments === 1){
            if('innerText' in node){
                return node.innerText
            }else{
                return node.textContent
            }
        }
    },
    html(node, string){    //è¯»å†™HTMLå†…å®¹
        if(arguments === 2){
            node.innerHTML = string
        }else if(arguments === 1){
            return node.innerHTML
        }
    },
    style(node, name, value){   //ä¿®æ”¹style
        if(arguments === 3){
            node.style[name] = value   //dom.style(div, 'color', 'red')
        }else{arguments === 2}{
            if(typeof name === 'string'){
                return node.style[name]   //dom.style(div, 'color')
            }else if(name instanceof Object){
                const object = name
                for(let key in object){
                    node.style[name] = object[name]
                }
            }
        }
    },
    class:{   
        add(node, className){    //æ·»åŠ class
            node.classList.add(className)
        },
        remove(node, className){   //åˆ é™¤class
            node.classList.remove(className)
        },
        has(node, className){
            return node.classList.container(className)
        }
    },
    on(node, eventName, fn){    //æ·»åŠ äº‹ä»¶ç›‘å¬
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn){    //åˆ é™¤äº‹ä»¶ç›‘å¬
        node.removeEventListener(eventName, fn)
    },
    //æŸ¥
    find(selector,scope){   //ç”¨äºè·å–æ ‡ç­¾æˆ–æ ‡ç­¾ä»¬
        return (scope || document).querySelectorAll(selector)
    },
    parent(node){  //ç”¨äºè·å–çˆ¶å…ƒç´ 
        return node.parentNode
    },
    children(node){   //ç”¨äºè·å–å­å…ƒç´ 
        return node.children
    },
    siblings(node){     //ç”¨äºè·å–å…„å¼Ÿå…ƒç´ 
        return Array.from(node.parentNode.children)
        .filter(n=>n!==node)
    },
    next(node){    //ç”¨äºè·å–å¼Ÿå¼Ÿ
        let x = node.nextSibling
        while(x && x.nodeType === 3){
            x =x.nextSibling
        }
        return x
    },
    previous(node){    //ç”¨äºè·å–å“¥å“¥
        let x = node.previousSibling
        while(x && x.nodeType === 3){
          x = x.previousSibling
        }
        return x
      },
      each(nodeList, fn){    //ç”¨äºéå†æ‰€æœ‰èŠ‚ç‚¹
        for(let i=0;i<nodeList.length;i++){
          fn.call(null, nodeList[i])
        }
      },
      index(node){    //ç”¨äºè·å–æ’è¡Œè€å‡ 
        const list = dom.children(node.parentNode)
        let i
        for(i=0;i<list.length;i++){
          if(list[i] === node){
            break
          }
        }
        return i
      }
    
} ;